<template>
  <div id="app">
    <div id="nav" v-if="isLoggedIn && showNav">
      <router-link to="/">Home</router-link> |
      <router-link to="/about">About</router-link>
      <router-link to="/chat">
      <img src="@/assets/gifs/email3.gif"/>
      </router-link>
    </div>
    <router-view />
  </div>
</template>

<script>
import chatService from "@/lib/chatService";
import userService from "@/lib/userService";
import minerService from "@/lib/minerService";

export default {
  async mounted() {
    const username = localStorage.getItem("username");
    if (username) {
      userService.loginUser(username);
    }
    chatService.registerListeners();
    minerService.registerListeners();
    this.$store.dispatch("Miner/initState");

    this.saveInterval = setInterval(() => {
      this.$store.dispatch("Miner/saveState");
    }, 5000);
  },
  data() {
    return {
      saveInterval: null,
    };
  },
  computed: {
    isLoggedIn() {
      return !!this.$store.getters["User/currentUser"];
    },
    showNav() {
      return ![
        'Ticker'
      ].includes(this.$route.name)
    }
  },
  beforeDestroy() {
    this.$store.dispatch("Miner/saveState");
    clearInterval(this.saveInterval);
  },
};
</script>

